import Jumbotron from 'react-bootstrap/Jumbotron';
import Card from 'react-bootstrap/Card';
import Button from 'react-bootstrap/Button';
import { Slide, Zoom } from 'react-awesome-reveal';
import { connect } from 'react-redux';
import './Projects.css';

const Projects = (props) => {
    return (
        <div className="projectsDiv" id="projects">
            <Jumbotron fluid className="projects m-0 p-3 mt-4">
                <Slide>
                    <h3 className="projectsLabel">Projects</h3>
                </Slide>
                <div className="projectsDiv">
                    {
                        props.projects.map(project => {
                            return (
                                <div className="projectsWrapper col-lg-4" key={project.title}>
                                    <Zoom>
                                        <Card className="projectCard col-sm-12 col-md-12 col-lg-12">
                                            <Card.Img className="projectPic" src={project.image} />
                                            <div className="description">
                                                <h2>{project.title}</h2>
                                                <h6>tools: {project.tools} </h6>
                                                <h6>{project.description}</h6>
                                                <a target="_blank" href={project.link} rel="noreferrer">
                                                    <Button className="projBtn mr-2" variant="outline-primary">View Github</Button>
                                                </a>
                                                <a target="_blank" href={project.deployLink} rel="noreferrer">
                                                    <Button className="projBtn" variant="outline-primary">View project</Button>
                                                </a>
                                            </div>
                                        </Card>
                                    </Zoom>
                                </div>
                            );
                        })
                    }
                </div>
            </Jumbotron>
        </div>
    );
}

const mapStateToProps = state => {
    return {
        projects: state.projects
    }
}

export default connect(mapStateToProps)(Projects);