import './Header.css'
import Navbar from 'react-bootstrap/Navbar'
import Nav from 'react-bootstrap/Nav'
import { Link, animateScroll as scroll } from 'react-scroll'

const Header = () => {

    const scrollToTop = () => {
        scroll.scrollToTop();
    }

    return (
        <div className="headerDiv">
            <Navbar fixed="top" bg="light" expand="lg">
                <Navbar.Brand className="portName" onClick={scrollToTop}>MY PORTFOLIO</Navbar.Brand>
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav">
                    <Nav className="links">
                        <Link className="navLinks" smooth to="about">
                            <Nav.Item>ABOUT</Nav.Item>
                        </Link>
                        <Link className="navLinks" smooth to="projects">
                            <Nav.Item>PROJECTS</Nav.Item>
                        </Link>
                        <Link className="navLinks" smooth to="contact">
                            <Nav.Item>CONTACT</Nav.Item>
                        </Link>
                    </Nav>
                </Navbar.Collapse>
            </Navbar>
        </div>
    );
}

export default Header;