import './Contact.css'
import { Jumbotron } from 'react-bootstrap'
import { FaEnvelopeOpenText } from 'react-icons/fa';
import { GoMarkGithub } from "react-icons/go";
import { IconContext } from "react-icons";

const Contact = () => {
    return (
        <Jumbotron fluid className="contactDiv text-center m-0 p-3" id="contact">
            <h3 className="contactLabel"> Contact </h3>
            <div className="contacts">
                <h5 className="contactIntro">Feel free to connect with me: </h5>
                <div className="contactContent col-sm-12">
                    <div className="contactInfoDiv">
                        <div className="contactInfoWrapper px-3">
                            <IconContext.Provider value={{ size: "3em", className: "contactIcons" }}>
                                <GoMarkGithub /> <span className="contactInfo">
                                    <a className="contactLink" target="_blank"
                                    href="https://github.com/michaeljacinto1">https://github.com/michaeljacinto1</a></span>
                            </IconContext.Provider>
                        </div>
                    </div>
                    <div className="contactInfoDiv">
                        <div className="contactInfoWrapper px-3">
                            <IconContext.Provider value={{ size: "3em", className: "contactIcons" }}>
                                <FaEnvelopeOpenText /> <span className="contactInfo"> michaeljacinto35@gmail.com </span>
                            </IconContext.Provider>
                        </div>
                    </div>
                </div>
            </div>
        </Jumbotron>
    );
}
export default Contact;