import './Main.css'
import banner from '../Assets/banner.png'
import Container from 'react-bootstrap/Container';
import { Zoom } from 'react-awesome-reveal'

const Main = () => {
    return (
        <div className="mainDiv" id="intro">
            <Container fluid className="bannerContainer p-0 h-100 mb-5">
                <div className="bannerText mt-5 col-sm-12 col-md-6">
                    <Zoom className="">
                        <h1 className="bannerIntro ml-md-5 col-sm-12 col-md-10 col-lg-10">
                            Hello, I'm Michael.<br />
                            I'm a full-stack web developer.
                        </h1>
                    </Zoom>
                </div>
                <div className="imgDiv col-sm-12 col-md-6">
                    <img src={banner} alt="banner" />
                </div>
            </Container>
        </div>
    );
}
export default Main;