import Jumbotron from 'react-bootstrap/Jumbotron';
import Card from 'react-bootstrap/Card';
import CardDeck from 'react-bootstrap/CardDeck'
import ProgressBar from 'react-bootstrap/ProgressBar'
import { Zoom, Slide, Fade } from 'react-awesome-reveal'
import { connect } from 'react-redux';
import { IconContext } from "react-icons";
import './About.css';

const About = (props) => {
    return (
        <Jumbotron fluid className="aboutMe" id="about">
            <Slide>
                <h3 className="aboutLabel">About Me</h3>
            </Slide>
            <div className="aboutContentDiv">
                <div className="aboutContent">
                    {/* <img className = "profilePic" src = {profile}/> */}
                    <Slide>
                        <p className="bioText col-sm-12 col-lg-7 w-100"> Hello, I'm Michael Jacinto. A career shifter who wants to pursue a career in the IT industry. Enrolled to a six-month part-time coding boot camp to have the necessary skills. Searching now for a job where I can grow, learn, and share my knowledge and/or skills.</p>
                    </Slide>
                </div>
                <div className="qualitiesDiv">
                    <CardDeck className="p-0">
                        {
                            props.qualities.map(quality => {
                                return (
                                    <div className="qualitiesWrapper col-sm-12 col-md-12 col-lg-4" key={quality.name}>
                                        <Zoom>
                                            <Card className="h-100 mx-auto mb-sm-3 mb-md-3 col-sm-12 col-md-9 col-lg-11 qualities ">
                                                <img className="qualitiesPic" src={quality.image} />
                                                <Card.Title className="cardTitle">{quality.name}</Card.Title>
                                            </Card>
                                        </Zoom>
                                    </div>
                                );
                            })
                        }
                    </CardDeck>
                </div>
            </div>
            <Fade>
                <Jumbotron className="skillSetDiv col-sm-12 col-md-12 col-lg-12">
                    <h3 className="aboutLabel">Skillset</h3>
                    <IconContext.Provider value={{ size: "10em", className: "icons" }}>
                        {
                            props.skillSets.map(skill => {
                                return (
                                    <div className="skillsSet col-sm-12 col-md-12 col-lg-4" key={skill.name}>
                                        <Card className="h-100 mx-auto mb-sm-3 mb-md-3 col-sm-12 col-md-9 col-lg-10 skills ">
                                            <h6><skill.icon /></h6>
                                        </Card>
                                    </div>
                                );
                            })
                        }
                    </IconContext.Provider>
                </Jumbotron>
            </Fade>
        </Jumbotron>
    );
}

const mapStateToProps = state => {
    return {
        qualities: state.qualities,
        skillSets: state.skillSets
    }
}

export default connect(mapStateToProps)(About);